import "./Header.css";
import React, { useState } from "react";
import { HashLink as Link } from "react-router-hash-link";

function Header() {
  const [isClicked, setIsClicked] = useState(false);

  function handleClose() {
    setIsClicked(false)
  }

  function handleOpen() {
    setIsClicked(true)
  }
  return (
    <header className="header">
      <p className="header__title">
        Kirill S.
      </p>
      <nav className="header__nav">
        <ul className="header__list">
          <li className="header__list-item">
            <Link to="#about" className="header__link">
              –û–±–æ –º–Ω–µ <span className="header__link-effect"></span>
            </Link>
          </li>
          <li className="header__list-item">
            <Link to="#projects" className="header__link">
              –ü—Ä–æ–µ–∫—Ç—ã <span className="header__link-effect"></span>
            </Link>
          </li>
          <li className="header__list-item">
            <Link to="#contacts" className="header__link">
              –ö–æ–Ω—Ç–∞–∫—Ç—ã <span className="header__link-effect"></span>
            </Link>
          </li>
        </ul>
      </nav>
      <button onClick={handleOpen} className="header__burger-menu">
        <span></span>
      </button>
      {
        isClicked ?
          <div className="header__menu">
            <Link to="#about" onClick={handleClose} className="header__menu-button">–û–±–æ –º–Ω–µ</Link>
            <Link to="#projects" onClick={handleClose} className="header__menu-button">–ü—Ä–æ–µ–∫—Ç—ã</Link>
            <Link to="#contacts" onClick={handleClose} className="header__menu-button">–ö–æ–Ω—Ç–∞–∫—Ç—ã</Link>
            <button onClick={handleClose} className="header__menu-close">&#x2716;</button>
          </div>
          : ''
      }
    </header>
  );
}

export default Header;
